import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 50px;
  border: none;
  background-color: #ffffff;
  padding: 10px;
  font-weight: bold;
  color: #000000;
  width: 100px;
  cursor: pointer;
  box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const StyledInput = styled.input`
padding: 10px 10px;
border-radius: 50px;
border: none;
background-color: #ffffff;
font-weight: bold;
color: #000000;
width: 300px;
box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
-webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
-moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
`;

export const StyledTable = styled.table`
padding: 20px;
border-radius: 5px;
border: 2px solid #000;
border-color: #ffffff;
// background-color: #ff819c;
font-weight: light;
color: #ffffff;
width: 500px;
height: 200px;
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledImg = styled.img`
  width: 200px;
  height: 200px;
  @media (min-width: 767px) {
    width: 350px;
    height: 350px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [feedback, setFeedback] = useState("Make NFT more decentralized");
  const [claimingNft, setClaimingNft] = useState(false);
  const [ipfsHash, setipfsHash] = useState('');
  const [name, setName] = useState('[Name]');

  const Homepage = () => {
    return(
      <div className="user__avatar">
          <svg version="1.1" preserveAspectRatio="xMinYMin meet" width="512" height="512" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
          <image xlinkHref={"https://ipfs.io/ipfs/" + ipfsHash} x="48" y="46" width="200" height="200"/>
          <image href="../examples/1.png" x="0" y="0" width="512" height="512"><animate attributeType="XML" attributeName="x" from="0" to="-512" dur="1s" calcMode="discrete" fill="freeze" begin="0s"></animate></image>
          <image href="../examples/1.png" x="-512" y="0" width="512" height="512"><animate id="1" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="0s;1.end+4s"></animate></image>
          <image href="../examples/2.png" x="-512" y="0" width="512" height="512"><animate id="2" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="0.5s;2.end+4s"></animate></image>
          <image href="../examples/3.png" x="-512" y="0" width="512" height="512"><animate id="3" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="1s;3.end+4s"></animate></image>
          <image href="../examples/4.png" x="-512" y="0" width="512" height="512"><animate id="4" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="1.5s;4.end+4s"></animate></image>
          <image href="../examples/5.png" x="-512" y="0" width="512" height="512"><animate id="5" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="2s;5.end+4s"></animate></image>
          <image href="../examples/6.png" x="-512" y="0" width="512" height="512"><animate id="6" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="2.5s;6.end+4s"></animate></image>
          <image href="../examples/7.png" x="-512" y="0" width="512" height="512"><animate id="7" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="3s;7.end+4s"></animate></image>
          <image href="../examples/8.png" x="-512" y="0" width="512" height="512"><animate id="8" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="3.5s;8.end+4s"></animate></image>
          <image href="../examples/9.png" x="-512" y="0" width="512" height="512"><animate id="9" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="4s;9.end+4s"></animate></image>
          <image href="../examples/10.png" x="-512" y="0" width="512" height="512"><animate id="10" attributeType="XML" attributeName="x" from="-512" to="0" dur="1s" calcMode="discrete" fill="remove" begin="4.5s;10.end+4s"></animate></image>
          <filter id="F"><feGaussianBlur in="SourceAlpha" stdDeviation="1.2" result="blur"/><feOffset in="blur" dx=".8" dy=".8" result="offsetBlur" /><feMerge><feMergeNode in="offsetBlur" /><feMergeNode in="SourceGraphic" /></feMerge></filter>
          <text text-anchor="end" font-family="SimHei, San Franscisco, -apple-system, sans-serif" font-size="32" x="492" y="130" fill="#6cffb9" filter="url(#F)">{name}</text>
          <line x1="0" y1="0" x2="512" y2="0" stroke="white" stroke-width="10"/>
          <line x1="0" y1="0" x2="0" y2="512" stroke="white" stroke-width="10"/>
          <line x1="512" y1="512" x2="0" y2="512" stroke="white" stroke-width="10"/>
          <line x1="512" y1="512" x2="512" y2="0" stroke="white" stroke-width="10"/>
        </svg>
      </div>
    )
  };

  const Stage = (_supply) => {
    if (_supply == 0) {
      return "Stage ?";
    }
    if (_supply >= 1 && _supply < 500) {
      return "Stage 0 (ID 1-500)";
    }
    if (_supply >= 500 && _supply < 2000) {
      return "Stage 1 (ID 501-2000)";
    }
    if (_supply >= 2000 && _supply < 5000) {
      return "Stage 2 (ID 2001-5000)";
    }
    if (_supply >= 5000 && _supply < 8000) {
      return "Stage 3 (ID 5001-8000)";
    }
    if (_supply >= 8000 && _supply < 9000) {
      return "Stage 4 (ID 8001-9000)";
    }
    if (_supply >= 9000 && _supply < 9500) {
      return "Stage 5 (ID 9001-9500)";
    }
    if (_supply >= 9500 && _supply < 9900) {
      return "Stage 6 (ID 9500-9900)";
    }
    if (_supply >= 9900 && _supply < 9999) {
      return "Stage 7 (ID 9901-9999)";
    }
    if (_supply >= 9999) {
      return "END";
    }
  
  }

  const Price = (_supply) => {
    if (_supply < 500) {
      return 0;
    }
    if (_supply >= 500 && _supply < 2000) {
      return 0.02;
    }
    if (_supply >= 2000 && _supply < 5000) {
      return 0.04;
    }
    if (_supply >= 5000 && _supply < 8000) {
      return 0.08;
    }
    if (_supply >= 8000 && _supply < 9000) {
      return 0.16;
    }
    if (_supply >= 9000 && _supply < 9500) {
      return 0.32;
    }
    if (_supply >= 9500 && _supply < 9900) {
      return 0.64;
    }
    if (_supply >= 9900 && _supply < 9999) {
      return 1.28;
    }
    if (_supply >= 9999) {
      return 100;
    }
  
  }

  const claimNFTs = (_ipfsHash, _name) => {
    
    setFeedback("Minting your VaporWave...");
    setClaimingNft(true);
    blockchain.smartContract.methods
      .create(_ipfsHash, _name)
      .send({
        to: "0xD140962054aC38084E933D8aFb5E83Be68Fc435e",
        from: blockchain.account,
        value: blockchain.web3.utils.toWei(Price(data.totalSupply).toString(), "ether"),
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        setFeedback(
          "Now you are a VaporWave artist."
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen>
      <s.Container flex={1} ai={"center"} style={{ padding: 24, backgroundColor: "#ff819c" }} image={"../bg.gif"}>
        <s.TextTitle
          style={{ 
            textAlign: "center", 
            fontSize: 28, 
            fontWeight: "bold", 
          }}
        >
          Create your own VaporWave
        </s.TextTitle>
        <s.SpacerMedium />
        <ResponsiveWrapper flex={1} style={{ padding: 24 }}>
          <s.Container flex={1} jc={"center"} ai={"center"}>
            <Homepage />
            <s.SpacerMedium />
            <s.TextTitle
              style={{ textAlign: "center", fontSize: 35, fontWeight: "bold" }}
            >
              {data.totalSupply}/9999
            </s.TextTitle>
            <s.SpacerSmall />
            <s.TextTitle
              style={{ textAlign: "center", fontSize: 20, fontWeight: "bold" }}
            >
              {Stage(data.totalSupply)}
            </s.TextTitle>
            <s.SpacerSmall />
            <s.TextTitle
              style={{ textAlign: "center", fontSize: 20, fontWeight: "bold" }}
            >
              Mint price: {Price(data.totalSupply)} ETH
            </s.TextTitle>
            <s.SpacerLarge />
            <StyledTable
              style={{ textAlign: "right", fontSize: 8}}
            >
              <tr>
                <td>Stage</td>
                <td>ID</td>
                <td>Price(ETH)</td>
                <td>Amount</td>
              </tr>
              <tr>
                <td>0</td>
                <td>1-500</td>
                <td>0</td>
                <td>500</td>
              </tr>
              <tr>
                <td>1</td>
                <td>501-2000</td>
                <td>0.02</td>
                <td>1500</td>
              </tr>
              <tr>
                <td>2</td>
                <td>2001-5000</td>
                <td>0.04</td>
                <td>3000</td>
              </tr>
              <tr>
                <td>3</td>
                <td>5001-8000</td>
                <td>0.08</td>
                <td>3000</td>
              </tr>
              <tr>
                <td>4</td>
                <td>8001-9000</td>
                <td>0.16</td>
                <td>1000</td>
              </tr>
              <tr>
                <td>5</td>
                <td>9001-9500</td>
                <td>0.32</td>
                <td>500</td>
              </tr>
              <tr>
                <td>6</td>
                <td>9501-9900</td>
                <td>0.64</td>
                <td>400</td>
              </tr>
              <tr>
                <td>7</td>
                <td>9901-9999</td>
                <td>1.28</td>
                <td>99</td>
              </tr>
            </StyledTable>

          </s.Container>
          <s.SpacerMedium />
          
          <s.Container
            flex={1}
            jc={"center"}
            ai={"center"}
            style={{ 
              backgroundColor: "rgba(0, 0, 0, 0.8)", 
              padding: 24,
              borderRadius: 24,
              border: "5px dashed #dc90fa",
            }}
          >
            {Number(data.totalSupply) == 9999 ? (
              <>
                <s.TextTitle style={{ textAlign: "center" }}>
                  The sale has ended.
                </s.TextTitle>
                <s.SpacerSmall />
                <s.TextDescription style={{ textAlign: "center" }}>
                  You can still find vaporwaves on{" "}
                  <a
                    target={"_blank"}
                    href={"https://opensea.io/collection/thevaporwaves"}
                  >
                    opensea.io
                  </a>
                </s.TextDescription>
              </>
            ) : (
              <>
                <s.TextTitle style={{ textAlign: "center" }}>
                  The first ever onchain DIY collection
                </s.TextTitle>
                <s.SpacerSmall />
                <s.TextDescription style={{ textAlign: "center" }}>
                  {feedback}
                </s.TextDescription>
                <s.SpacerMedium />
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription style={{ textAlign: "center" }}>
                      Connect to the Ethereum network
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      CONNECT
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription style={{ textAlign: "center" }}>
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (                
                  <s.Container ai={"center"} jc={"center"}>

                    <s.SpacerLarge /><s.SpacerLarge />
                    <s.TextDescription style={{ textAlign: "center" }}>
                        Step1: Enter IPFS Hash of your NFT which will be a part of your VaporWave.
                    </s.TextDescription>
                    <s.SpacerSmall />      

                    <StyledInput type="text" onChange={e => setipfsHash(e.target.value)} maxLength={80} placeholder="Enter IPFS Hash" >
                    </StyledInput>
                    <s.SpacerSmall />
                    
                    <s.TextDescription style={{ textAlign: "center" }}>
                      *You can also{" "}
                      <a
                        target={"_blank"}
                        href={"https://anarkrypto.github.io/upload-files-to-ipfs-from-browser-panel/public/"}
                      >
                        get IPFS Hash 
                      </a>
                      {" "}by uploading your local image. 
                    </s.TextDescription>
                    
                    <s.SpacerLarge /><s.SpacerLarge />
                    <s.TextDescription style={{ textAlign: "center" }}>
                        Step2: Enter text within 15 characters which will be engraved on your VaporWave forever.
                    </s.TextDescription>
                    <s.SpacerSmall /> 

                    <StyledInput type="text" onChange={e => setName(e.target.value)} maxLength={15} placeholder="Name your collection">
                    </StyledInput> 
                  
                    <s.SpacerLarge /><s.SpacerLarge />
                    <s.TextDescription style={{ textAlign: "center" }}>
                        Step3: Mint the NFT when the Hash you entered has been displayed correctly on the preview.
                    </s.TextDescription>
                    
                    <s.SpacerXSmall />
                    <s.TextDescription style={{ textAlign: "center" }}>
                        (It may takes 1 min after entering the hash.)
                    </s.TextDescription>
                    
                    <s.SpacerSmall />

                    <StyledButton
                      disabled={claimingNft ? 1 : 0}
                      onClick={(e) => {
                        e.preventDefault();
                        claimNFTs(ipfsHash, name);
                        getData();
                      }}
                    >
                      {claimingNft ? "BUSY" : "Mint"}
                    </StyledButton>
 
                    <s.SpacerLarge /><s.SpacerLarge />
                    <s.TextDescription style={{ textAlign: "center" }}>
                        Step4: Check your VaporWave on{" "}
                  <a
                    target={"_blank"}
                    href={"https://opensea.io/collection/thevaporwaves"}
                  >
                    opensea.io
                  </a>

                    </s.TextDescription>
                    <s.SpacerSmall />
                    <s.TextDescription style={{ textAlign: "center" }}>
                    VaporWave uses an unprecedented NFT generation method. Currently Opensea makes errors when displaying VaporWaves. You can still view your creations in the following ways:
                    </s.TextDescription>
                    <s.SpacerLarge />
                    <s.TextDescription style={{ textAlign: "center" }}>
                      Right-click the image on Opensea and select "Open image in new tab".
                    </s.TextDescription>
                    <s.SpacerXSmall />
                    <s.TextDescription style={{ textAlign: "center" }}>
                      OR
                    </s.TextDescription>
                    <s.SpacerXSmall />
                    <s.TextDescription style={{ textAlign: "center" }}>
                    Right-click the image on Opensea and select "Inspect", change the "{"<img"}" to "{"<embed"}".
                    </s.TextDescription>
                    <s.SpacerLarge />
                  </s.Container>
                )}
              </>
            )}
          </s.Container>
        </ResponsiveWrapper>
        <s.SpacerSmall />
        <s.Container jc={"center"} ai={"center"} style={{ width: "70%" }}>
          <s.TextDescription style={{ textAlign: "center", fontSize: 9 }}>
            Anyone can build their own community to discuss VaporWave. Let's make NFT more decentralized!
          </s.TextDescription>
          <s.SpacerSmall />
        </s.Container>
      </s.Container>
    </s.Screen>
  );
}

export default App;
